    //       rightArrow = document.querySelector('.offer__slider-next'),
    //       slideNumber = document.querySelector('#current');
        
    // let currentSlideNumber = parseInt(slideNumber.textContent);

    // slides.forEach((item, i) => {
    //     item.classList.add('hide');
    //     if (i == currentSlideNumber - 1) {
    //         item.classList.remove('hide');
    //         item.classList.add('show');
    //     }
    // });

    // function prevSlide() {
    //     if (currentSlideNumber == 1) {
    //         slides[currentSlideNumber - 1].classList.remove('show');
    //         slides[currentSlideNumber - 1].classList.add('hide');
    //         slides[slides.length - 1].classList.remove('hide');
    //         slides[slides.length - 1].classList.add('show');
    //     } else {
    //         slides[currentSlideNumber - 1].classList.remove('show');
    //         slides[currentSlideNumber - 1].classList.add('hide');
    //         slides[currentSlideNumber - 2].classList.remove('hide');
    //         slides[currentSlideNumber - 2].classList.add('show');
    //     }
    // }

    // function nextSlide() {
    //     if (currentSlideNumber == slides.length) {
    //         slides[currentSlideNumber - 1].classList.remove('show');
    //         slides[currentSlideNumber - 1].classList.add('hide');
    //         slides[0].classList.remove('hide');
    //         slides[0].classList.add('show');
    //     } else {
    //         slides[currentSlideNumber - 1].classList.remove('show');
    //         slides[currentSlideNumber - 1].classList.add('hide');
    //         slides[currentSlideNumber].classList.remove('hide');
    //         slides[currentSlideNumber].classList.add('show');
    //     }

